@import '../../node_modules/bootstrap/scss/_functions';
@import '../../node_modules/bootstrap/scss/_variables';
@import '../../node_modules/bootstrap/scss/_mixins';
@import 'configs/_custom';

.dwarf-menu {
	background-color: $gray-100;
	font-size: $font-size-sm;

	@media (max-width: 480px) {
		display: none; } }

.dwarf-menu__padding {
	$padding-devider: $font-size-root-number * $font-size-sm-number;
	padding: #{$grid-gutter-width / 4} 0;
	padding: 1em * (number(str-replace(#{$grid-gutter-width / 4}, unit($font-size), '')) / $padding-devider) 0;
	padding: calc(1em * (#{number(str-replace(#{$grid-gutter-width / 4}, unit($font-size), ''))} / #{$padding-devider})) 0; }

.dwarf-menu__row {
	&:not(:empty) {
		font-size: 0;
		line-height: 0;
		letter-spacing: -1px;
		margin: '-#{$grid-gutter-width}' / 8 '-#{$grid-gutter-width}' / 4; } }

.dwarf-menu__align {
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap; }

.dwarf-menu__item {
	display: inline-block;
	padding: 0 '#{$grid-gutter-width}' / 4;
	font-size: $font-size-base;
	line-height: $line-height-base;
	color: $gray-600;
	margin: '#{$grid-gutter-width}' / 8 0; }

.dwarf-menu__link {
	display: block;
	color: inherit;
	font-size: inherit;

	&:hover,
	&:focus {
		color: $gray-600; } }

@media (min-width: $s) {
	.dwarf-menu__align {
		justify-content: space-between; } }
