.dwarf-menu {
				background-color: #edf3fa;
				font-size: 0.75rem;
}

@media (max-width: 480px) {
				.dwarf-menu {
								display: none;
				}
}

.dwarf-menu__padding {
				padding: 8px 0;
				padding: 0.66667em 0;
				padding: calc(1em * (8 / 12)) 0;
}

.dwarf-menu__row:not(:empty) {
				font-size: 0;
				line-height: 0;
				letter-spacing: -1px;
				margin: -4px -8px;
}

.dwarf-menu__align {
				text-align: center;
				display: flex;
				justify-content: center;
				align-items: center;
				flex-wrap: wrap;
}

.dwarf-menu__item {
				display: inline-block;
				padding: 0 8px;
				font-size: 0.875rem;
				line-height: 1.14286;
				color: #718391;
				margin: 4px 0;
}

.dwarf-menu__link {
				display: block;
				color: inherit;
				font-size: inherit;
}

.dwarf-menu__link:hover, .dwarf-menu__link:focus {
				color: #718391;
}

@media (min-width: 600px) {
				.dwarf-menu__align {
								justify-content: space-between;
				}
}
